<template>
	<div id="app">
		<app-header>Vhost-manager</app-header>
		<div class="container">
			<item-creator @app:create="createItem" @app:search="searchHost" />
			<div class="list">
				<app-item
				v-for="(host, index) in visibleHosts" :key="index"
				:host="host"
				:id="index"
				@app:delete="deleteItem"
			/>
			</div>
		</div>
	</div>
</template>

<style>
