<script>
import appHeader from './components/Header.vue';
import appItem from './components/HostItem.vue'
import toastr from 'toastr';
// import { setInterval } from 'timers';
import itemCreator from './components/ItemsCreator.vue'

toastr.options = {
	"closeButton": false,
	"debug": false,
	"newestOnTop": false,
	"progressBar": false,
	"positionClass": "toast-top-right",
	"preventDuplicates": false,
	"onclick": null,
	"showDuration": "300",
	"hideDuration": "1000",
	"timeOut": "5000",
	"extendedTimeOut": "1000",
	"showEasing": "swing",
	"hideEasing": "linear",
	"showMethod": "fadeIn",
	"hideMethod": "fadeOut"
}

const PORT = "8080";
const URL = `http://localhost:${PORT}/api`;
// const URL = 'http://b3080552.ngrok.io/api';

export default {
  name: 'app',
  components: {
	appHeader,
	appItem,
	itemCreator
  },
  data() {
	return {
		status: 'stopped',
		hosts: [],
    visibleHosts: [],
		msg: ''
	}
  },
  watch: {
	msg: {
		handler(obj) {
			if(obj.error == '') {
				this.showData(obj.data);
			} else {
				this.showError(obj.error);
			}
		},
		deep: true
	},
	hosts() {
		this.visibleHosts = this.hosts;
	}
  },
  methods: {
    searchHost(name) {
      if(name.trim() === '') {
        this.visibleHosts = this.hosts;
      } else {
        this.visibleHosts = this.hosts.filter(host => {
          return host.url.includes(name);
        })
      }
    },
	async getStatus() {
		const res = await fetch(`${URL}/status`)
		const json = await res.json();
		this.status = json.status;
	},
	async getHosts() {
		await this.getStatus();
		if(this.status == 'active') {
			const res = await fetch(`${URL}/list`);
			const json = await res.json();
			this.hosts = json;
		} else {
			throw new Error('Serves is disabled');
		}
	},
	async deleteItem(url) {
		const delObj = {name: url}
		this.hosts = this.hosts.filter( el => el.url != url );
		const res = await fetch(`${URL}/delete`, {method: 'POST', body: JSON.stringify(delObj)});
		const json = await res.json();
		this.msg = json;
	},
	async createItem(url) {
		if(this.hosts[url] === undefined) {
			url = url.trim().replace(/ /g, '.');
			url = url.replace(/http:\/\//g, '')
			url = url.replace(/https:\/\//g, '')
			const creOnj = {name: url}
			const res = await fetch(`${URL}/create`, {method: 'POST', body: JSON.stringify(creOnj)});
			const json = await res.json();
			this.msg = json;
		} else {
			toastr.error("This host is already exists");
		}
    await this.getHosts();
	},
	showData(value) {
		toastr.success(value.normalize())
	},
	showError(value) {
		toastr.error(value.normalize());
	}
  },
	async mounted() {
    await this.getHosts();
    this.visibleHosts = this.hosts;
    setInterval(this.getHosts, 30000); //TODO: Переписать на рекурсивный Timeout
    // setTimeout( function interval() {
    //   this.getHosts();
    //   setTimeout(interval, 10000)
    // }, 10000);
  }
}
</script>

<style>
		.list {
			margin-top: 40px;
		}
		.title-wrapper {
			display: flex;
			justify-content: center;
		}
		.title {
			font-size: 3em;	
		}
</style>
